import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import Chip from "./Chip";


class ChipsPanel extends Component {

  constructor(props) {
      super(props);

      this.state = {
        color: 'red'
      };

  }

  render_label(number){

    if(number >= 0 && number <= 36){
      return this.props.number;
    }

  }


    render() {

      return (
        <spam className={`square ${this.state.color}`} onClick={this.cellClick} >
          {this.render_label(this.props.number)}
        </spam>
      );
    }

  }


  export default withRouter(ChipsPanel);
