import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import BetCell from './BetCell';


class RWheel extends Component {

    static image;
    static ctx;
    static num_angles = {
      5: 0+3.051,
      10: 0.169*1+3.051,
      23: 0.169*2+3.051,
      8: 0.169*3+3.051,
      30: 0.169*4+3.051,
      11: 0.169*5+3.051,
      36: 0.169*6+3.051,
      13: 0.169*7+3.051,
      27: 0.169*8+3.051,
      6: 0.169*9+3.051,
      34: 0.169*10+3.051,
      17: 0.169*11+3.051,
      25: 0.169*12+3.051,
      2: 0.169*13+3.051,
      21: 0.169*14+3.051,
      4: 0.169*15+3.051,
      19: 0.169*16+3.051,
      15: 0.169*17+3.051,
      32: 0.169*18+3.051,
      0: 0.169*19+3.051,
      26: 0.169*20+3.051,
      3: 0.169*21+3.051,
      35: 0.169*22+3.051,
      12: 0.169*23+3.051,
      28: 0.169*24+3.051,
      7: 0.169*25+3.051,
      29: 0.169*26+3.051,
      18: 0.169*27+3.051,
      22: 0.169*28+3.051,
      9: 0.169*29+3.051,
      31: 0.169*30+3.051,
      14: 0.169*31+3.051,
      20: 0.169*32+3.051,
      1: 0.169*33+3.051,
      33: 0.169*34+3.051,
      16: 0.169*35+3.051,
      24: 0.169*36+3.051
    };


  constructor(props) {
      super(props);

      var black = [15, 4, 2, 17, 6, 13, 11, 8, 10, 24, 33, 20, 31, 22, 29, 28, 35, 26];
      var red = [32, 19, 21, 25, 34, 27, 36, 30, 23, 5, 16, 1, 14, 9, 18, 7, 12, 3];

      

      this.mainLoop = this.mainLoop.bind(this);
      this.drawImage = this.drawImage.bind(this);

  }

  drawSelector(ctx){

    ctx.resetTransform();

    ctx.beginPath();
    ctx.moveTo(110, 5);
    ctx.lineTo(123, 25);
    ctx.lineTo(132, 5);
    ctx.lineTo(120, 5);
    //ctx.stroke();

    // the outline
    ctx.lineWidth = 1;
    ctx.strokeStyle = '#666666';
    ctx.stroke();

    // the fill color
    ctx.fillStyle = "#FFCC00";
    ctx.fill();

    ctx.closePath();

  }

    drawImage(image, x, y, cx, cy, angle){

      var ctx = RWheel.ctx;
      var image = RWheel.image;

      if(!this.props.mustSpin){

        ctx.setTransform(1, 0, 0, 1, x, y);  // set the rotation origin
        ctx.rotate(RWheel.num_angles[this.props.result]); // rotate
        ctx.drawImage(image,-cx,-cy);

        this.drawSelector(ctx);
        return;
      }

        

        ctx.setTransform(1, 0, 0, 1, x, y);  // set the rotation origin
        ctx.rotate(angle); // rotate
        ctx.drawImage(image,-cx,-cy); // draw image offset to put cx,cy at the point of rotation
        ctx.setTransform(1,0,0,1,0,0); // restore the transform

        this.drawSelector(ctx);

    }


    mainLoop(time){ // time is passed by  requestAnimationFrame

        var ctx = RWheel.ctx;
        var image = RWheel.image;

        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height); // clear 


        if(image.complete){
            this.drawImage(image, 125, 125, image.width / 2, image.height/2, (time / 5000) * Math.PI * 2); // rotate one every 5 seconds

        }

        
        requestAnimationFrame(this.mainLoop);
    }

    componentDidMount() {
      RWheel.image = new Image;
      RWheel.image.src = "roulette5a.png";

      var myCanvas = document.getElementById('myCanvas');
      RWheel.ctx = myCanvas.getContext("2d");

      requestAnimationFrame(this.mainLoop);

      // setInterval( function() {  RWheel.angle = (RWheel.angle + 1); }, 100 );
    }
    

    render() {

      var indents = [];

      if(this.props.result >=0 ){
        indents.push(  <BetCell key={this.props.result} is_simple={true} number={this.props.result} updateBet={() => {}} chipBet={0} disabled={true} /> );
      }

      return (
        <span>
          <canvas id="myCanvas" width={'250px'} height={'250px'} ></canvas>

         {indents}
        </span>
      );
    }

  }


export default withRouter(RWheel);
