import React, { Component } from "react";
import {dataService} from './user-context';
import { withRouter } from 'react-router-dom';

declare var $ : any;

const Web3 = require('web3');

const networks_info = {
  56: {pname: "BSC MainNet", name: "bsc", hex: "0x"+(56).toString(16), symbol: 'BNB', decimals: 18, icon: 'icon-play-bsc',
  rpc: 'https://bsc-dataseed1.binance.org', explorer: 'https://bscscan.com/'
  },
  97: {pname: "BSC TestNet", name: "bsc_testnet", hex: "0x"+(97).toString(16), symbol: 'BNB', decimals: 18, icon: 'icon-play-bsc',
  rpc: 'https://data-seed-prebsc-1-s2.binance.org:8545/', explorer: 'https://testnet.bscscan.com/'
  },
  137: {pname: "Polygon MainNet", name: "polygon", hex: "0x"+(137).toString(16), symbol: 'MATIC', decimals: 18, icon: 'icon-play-matic',
  rpc: 'https://polygon-rpc.com', explorer: 'https://polygonscan.com'
  },
  80001: {pname: "Polygon TestNet", name: "polygon_testnet", hex: "0x"+(80001).toString(16), symbol: 'MATIC', decimals: 18, icon: 'icon-play-matic',
  rpc: 'https://matic-mumbai.chainstacklabs.com', explorer: 'https://mumbai.polygonscan.com/'
  },
}


class Web3Comp extends Component {

  static networks = {
    "bsc_testnet": "0x"+(97).toString(16),
    "bsc": "0x"+(56).toString(16),
    "polygon": "0x"+(137).toString(16),
    "polygon_testnet": "0x"+(80001).toString(16),
  };

  static target_net = 56; //BSC MainNet

componentDidMount() {

  const m = this.detectMetaMask();

  if(m) {
      //this.loadBlockchainData();

      window.addEventListener('load', function(e) {
        this.loadBlockchainData();
    }.bind(this), false);


  } else {
    //alert(m);
      $('#status').html('Metamask Required!');
      $('#mySmallModalLabel').modal('show');
  }

}

async swap_network(network_id){

  Web3Comp.target_net = network_id;

  let network_name_set = networks_info[network_id]['pname']

  this.setState({network_name: network_name_set});

  localStorage.setItem('selectedNetwork', Web3Comp.target_net);
  localStorage.setItem('network_name', network_name_set);
  

  await this.NetworkFit();
  this.refresh();

}

async loadBlockchainData() {

    await this.connect();
    this.NetworkFit();

    // detect Metamask account change
    window.ethereum.on('accountsChanged', function (accounts) {
      console.log('accountsChanges',accounts);
      this.handleAccountsChanged(accounts);
    }.bind(this));

    // detect Network account change
   window.ethereum.on('networkChanged', function(networkId){
     console.log('networkChanged',networkId);
     this.NetworkFit();
   }.bind(this));

}

refresh(){
  let location = window.location.pathname.replace("/", "")
  this.props.history.push("/");
  setTimeout(() => this.props.history.push(location), 1);
}


constructor(props) {
  super(props)
  this.state = {
    currentAccount: '',
    web3: null,
    abi: '',
    bytecode: '',
    network_name: 'BSC MainNet',
    network_icon: 'icon-play-bsc',
    redirect: false
  }

}

render() {

  return (
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">

        </ul>

        <div class="btn-group">
          <button type="button" class="btn btn-success dropdown-toggle" data-toggle="dropdown" aria-expanded="false">
            Network ({this.state.network_name}) <i className={this.state.network_icon} ></i>
          </button>
          <div class="dropdown-menu">
            <a className={(this.state.network_name=='BSC MainNet') ? "dropdown-item active" : "dropdown-item"}
              onClick={this.swap_network.bind(this, 56)} >
              <i class="icon-play-bsc" ></i> BSC MainNet
            </a>
            <a className={(this.state.network_name=='BSC TestNet') ? "dropdown-item active" : "dropdown-item"}
              onClick={this.swap_network.bind(this, 97)} >
              <i class="icon-play-bsc" ></i> BSC TestNet
            </a>
            <a className={(this.state.network_name=='Polygon MainNet') ? "dropdown-item active" : "dropdown-item"}
              onClick={this.swap_network.bind(this, 137)} >
              <i class="icon-play-matic" ></i> Polygon MainNet
            </a>
            <a className={(this.state.network_name=='Polygon TestNet') ? "dropdown-item active" : "dropdown-item"}
              onClick={this.swap_network.bind(this, 80001)} >
              <i class="icon-play-matic" ></i> Polygon TestNet
            </a>
          </div>
        </div>

        <ul class="navbar-nav mr-2">

        </ul>

        <div class="btn-group">
          <button type="button" class="btn btn-success" id="connect_bt" onClick={this.connect.bind(this)} > 
          <i className={this.state.network_icon} ></i> 
          { this.state.currentAccount.length > 10 ?
            (
              <span>Connected To: 
                <b>{this.state.currentAccount.substring(0, 6) + "..." + this.state.currentAccount.substring(this.state.currentAccount.length-4, this.state.currentAccount.length)}
                </b>
              </span>

            ):(
              <span>Connect</span>
            )
          }
          </button>
        </div>

    </div>
  );
}

async NetworkFit(){

  var target_net =  Web3Comp.target_net; //bsc_mainnet;

  let selectedNetwork = localStorage.getItem('selectedNetwork')
  if(selectedNetwork != null && networks_info[selectedNetwork] != null){
    target_net = selectedNetwork;

    this.setState({network_name: localStorage.getItem('network_name'), network_icon: networks_info[selectedNetwork]['icon']});
  }


  if(this.state.web3 == null){
    return;
  }

  var netid = await this.state.web3.eth.net.getId();

  if(netid == networks_info[target_net]['hex']){
    return true;
  }

  try {
        await window.ethereum.request({
              method: 'wallet_switchEthereumChain',
              params: [{ chainId: networks_info[target_net]['hex'] }],
            }).then((handleAccountsChanged) => {
              dataService.setData({ web3: this.state.web3, currentAccount: this.state.currentAccount});
              this.setState({network_icon: networks_info[target_net]['icon']});

            })
  } catch (switchError) {
    // This error code indicates that the chain has not been added to MetaMask.
    if (switchError.code == 4902 || switchError.code == -32603) {
      try {

        let params;

        params = [{
          chainId: networks_info[target_net]['hex'],
          chainName: networks_info[target_net]['pname'],
          nativeCurrency: {
            name: networks_info[target_net]['symbol'],
            symbol: networks_info[target_net]['symbol'],
            decimals: networks_info[target_net]['decimals']
          },
          rpcUrls: [networks_info[target_net]['rpc']],
          blockExplorerUrls: [networks_info[target_net]['explorer']]
        }];

        await window.ethereum.request({
          method: 'wallet_addEthereumChain',
          params: params,
        });
      } catch (addError) {
        // handle "add" error
        console.log(addError);
      }
    }
    // handle other "switch" errors
  }
}

 async handleAccountsChanged(accounts) {
    console.log('Calling HandleChanged')

    if (accounts.length === 0) {
        console.log('Please connect to MetaMask.');
        this.setState({currentAccount: ''});
        $('#status').html('Connect with Metamask');
        //$('#connect_bt').html(`<i class=${this.state.network_icon} ></i> Connect`);
        $('#mySmallModalLabel').modal('show');
    } else if (accounts[0] !== this.state.currentAccount) {
        this.state.currentAccount = accounts[0];
        //$('#connect_bt').html(`<i class=${this.state.network_icon} ></i> Connect`);
        $('#status').html('')

        if(this.state.currentAccount != null) {
            // Set the button label
            let label = this.state.currentAccount;
            label = label.substring(0, 6) + "..." + label.substring(label.length-4, label.length);
            //$('#connect_bt').html(`<i class=${this.state.network_icon} ></i> Connected To: <b>` + label + "</b>");
            console.log(label);

        }
    }
    console.log('WalletAddress in HandleAccountChanged ='+this.state.currentAccount);
    dataService.setData({ web3: this.state.web3, currentAccount: this.state.currentAccount});
}

async  connect() {

    let web3 = null;
   // Modern dapp browsers...
   if (window.ethereum) {
     try {
       // Request account access if needed
       const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
       web3 = new Web3(window.ethereum);
       // Accounts now exposed
     } catch (error) {
       console.log(error);
           // EIP-1193 userRejectedRequest error
           // If this happens, the user rejected the connection request.
           console.log('Please connect to MetaMask.');
           $('#status').html('You refused to connect Metamask!');
           $('#mySmallModalLabel').modal('show');
           //$('#connect_bt').html(`<i class=${this.state.network_icon} ></i> Connect`);
           return;
       
     }
   }
   // Legacy dapp browsers...
   else if (window.web3) {
     // Use Mist/MetaMask's provider.
     web3 = window.web3;
     console.log("Injected web3 detected.");
   }
   // Fallback to localhost; use dev console port by default...
   else {
     const provider = new Web3.providers.HttpProvider(
       "http://127.0.0.1:8545"
     );
     web3 = new Web3(provider);
     console.log("No web3 instance injected, using Local web3.");
   }

   const accounts = await web3.eth.getAccounts();
   this.setState({ web3: web3});

   await this.NetworkFit();
   await this.handleAccountsChanged(accounts);
   await this.NetworkFit();
   await dataService.setData({ web3: web3, currentAccount: this.state.currentAccount});

}

 detectMetaMask() {
    if (typeof window.ethereum !== 'undefined') {
        return true
    } else {
        return false
    }
}



}

export default withRouter(Web3Comp);
