import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';


class Chip extends Component {

  constructor(props) {
      super(props);



  }

  render_label(number){

      return this.props.value;

  }

    render() {

      var selected = ((this.props.selected) ? 'selected-chip' : '');

      return (
        <span className={`chip ${selected}`} onClick={this.props.updateChip} data-toggle="tooltip" data-placement="bottom" title="Double click to edit" >
          {this.render_label(this.props.value)}
        </span>
      );
    }

  }


  export default withRouter(Chip);
