import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';


const range = (start, stop, step) => Array.from({ length: (stop - start) / step + 1}, (_, i) => start + (i * step));


class BetCell extends Component {

  static cellTypes = {
    37: range(3, 36, 3),
    38: Array.from(range(3, 36, 3), x => x - 1),
    39: Array.from(range(3, 36, 3), x => x - 2),
    40: range(1, 12, 1),
    41: range(13, 24, 1),
    42: range(25, 36, 1),
    43: range(1, 18, 1),  // 1 to 18
    48: range(19, 36, 1), // 19 to 36
    44: range(2, 36, 2), // EVEN
    45: [32, 19, 21, 25, 34, 27, 36, 30, 23, 5, 16, 1, 14, 9, 18, 7, 12, 3],
    46: [15, 4, 2, 17, 6, 13, 11, 8, 10, 24, 33, 20, 31, 22, 29, 28, 35, 26],
    47: range(1, 35, 2), // ODD
    0: 0
  }

  constructor(props) {
      super(props);

      var color = 'green';

      if(this.props.is_simple) color = 'green-simple';

      var black = [15, 4, 2, 17, 6, 13, 11, 8, 10, 24, 33, 20, 31, 22, 29, 28, 35, 26];
      var red = [32, 19, 21, 25, 34, 27, 36, 30, 23, 5, 16, 1, 14, 9, 18, 7, 12, 3];
      var twoto1 = [37, 38, 39];
      var ternary = [40, 41, 42];
      var sext = [43, 44, 45, 46, 47, 48];
      
      if(black.includes(this.props.number)){
        color = 'black';
      }
      
      if(red.includes(this.props.number)){
        color = 'red';
      }

      if(twoto1.includes(this.props.number)){
        color = 'twoto1';
      }

      if(ternary.includes(this.props.number)){
        color = 'ternary';
      }

      if(sext.includes(this.props.number)){
        color = 'sext1';
      }

      this.state = {
        color: color,
        bet: 0
      };

      this.cellClick = this.cellClick.bind(this);

  }

  static pretty_bet(bet_amount){

    if(bet_amount >= 1000000){
      return (bet_amount/1000).toFixed(1).toString() + " M"
    }

    if(bet_amount >= 1000){
      return (bet_amount/1000).toFixed(1).toString() + " K"
    }

    return bet_amount.toFixed(2);
    
  }

  render_label(number, is_simple=false){

    if(is_simple && number >= 0 && number <= 36){
      return this.props.number;
    }

    if(this.state.bet > 0 && number == 0){

      return <span className={`chip-bet chip-bet-zero`}>
                {BetCell.pretty_bet(this.state.bet)}
              </span>;

    }

    if(this.state.bet > 0 && number >= 43){

      return <span className={`chip-bet chip-bet-six`}>
                {BetCell.pretty_bet(this.state.bet)}
              </span>;

    }

    if(this.state.bet > 0 && number >= 40 ){

      var lab = '';

      if(number == 40){
        lab = '1 st 12';
      }
  
      if(number == 41){
        lab = '2 nd 12';
      }
  
      if(number == 42){
        lab = '3 rd 12';
      }

      return <span> 
        <span className={`chip-bet`}> {BetCell.pretty_bet(this.state.bet)}</span>
        {lab}
      </span>;

    }

    if(this.state.bet > 0 ){

      return <span className={`chip-bet`}>
                {BetCell.pretty_bet(this.state.bet)}
              </span>;

    }

    if(number >= 0 && number <= 36){
      return this.props.number;
    }

    if(number >= 37 && number <= 39){
      return '2to1';
    }

    if(number == 40){
      return '1 st 12';
    }

    if(number == 41){
      return '2 nd 12';
    }

    if(number == 42){
      return '3 rd 12';
    }

    if(number == 43){
      return '1 to 18';
    }

    if(number == 44){
      return 'EVEN';
    }

    if(number == 45){
      return <div class="rombo-red" ></div>;
    }

    if(number == 46){
      return <div class="rombo-black" ></div>;
    }

    if(number == 47){
      return 'ODD';
    }

    if(number == 48){
      return '19 to 36';
    }

  }


  componentDidUpdate(prevProps){

    if(prevProps.clear != this.props.clear && this.props.clear){

      this.setState({
        bet: 0
      });
      this.state.bet = 0;
      
    }
  }


  cellClick(){

    console.log('cellClick: ' + this.props.number);

    this.props.updateBet(this.props.number);

    if(this.props.disabled){
      return;
    }

    if(this.props.chipBet > 0){
      this.setState({bet: this.state.bet + this.props.chipBet});
    }

  }

    render() {

      var black = [15, 4, 2, 17, 6, 13, 11, 8, 10, 24, 33, 20, 31, 22, 29, 28, 35, 26];
      var red = [32, 19, 21, 25, 34, 27, 36, 30, 23, 5, 16, 1, 14, 9, 18, 7, 12, 3];
      let comp_color = '';

      
      if(black.includes(this.props.number)){
        this.state.color = 'black';
      }
      
      if(red.includes(this.props.number)){
        this.state.color ='red';
      }

      if(this.props.result == this.props.number){
        comp_color = " winner-cell";
      }

      if(this.props.is_simple){
        comp_color = " square-small";
      }

      return (
        <span className={`square ${this.state.color} ${comp_color}`} onClick={this.cellClick} result={this.props.result} >
          {this.render_label(this.props.number, this.props.is_simple)}
        </span>
      );
    }

  }


  export default withRouter(BetCell);
