import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import {abi as abi_dcas, networks as networks_dcas} from '../../contracts/DecCas.json';
import {dataService} from '../user-context';
import { BigNumber } from "bignumber.js";
import { BN } from "bn.js";

import busd from '../../images/busd50x50.png';
import olk from '../../images/olk.png';
import usdc from '../../images/usdc.png';


import BetCell from "./BetCell";


class Play extends Component {

    static token_info = {
        "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56": {
            'precision': 2,
            'decimals': 18,
            'label': <span>BUSD <img src={busd} width={25} height={25} /></span>,
            'explorer': 'https://bscscan.com/',
            'name': 'BUSD'
        },
        "0x78867BbEeF44f2326bF8DDd1941a4439382EF2A7": {
            'precision': 2,
            'decimals': 18,
            'label': <span>BUSD TestNet<img src={busd} width={25} height={25} /></span>,
            'explorer': 'https://testnet.bscscan.com/',
            'name': 'BUSD TestNet'
        },
        "0x4A6362D703c4AD619ddfDBf385Ff8D133af7AB41": {
            'precision': 2,
            'decimals': 18,
            'label': <span>OLK <img src={olk} width={25} height={25} /></span>,
            'explorer': 'https://bscscan.com/',
            'name': 'OLK'
        },
        "0x42a754606b4C5125429EF8063Da4A17803658077": {
            'precision': 2,
            'decimals': 18,
            'label': <span>OLK TestNet<img src={olk} width={25} height={25} /></span>,
            'explorer': 'https://testnet.bscscan.com/',
            'name': 'OLK TestNet'
        },
        "0xebbF9dD6d2b687dCF76B2b58dde56AB37D92d601": {
            'precision': 2,
            'decimals': 18,
            'label': <span>OLK TestNet<img src={olk} width={25} height={25} /></span>,
            'explorer': 'https://mumbai.polygonscan.com/',
            'name': 'OLK TestNet'
        },
        "0x046Ea590745aBd3F86b232Dbb51ca9847Da21751": {
            'precision': 2,
            'decimals': 18,
            'label': <span>OLK <img src={olk} width={25} height={25} /></span>,
            'explorer': 'https://polygonscan.com/',
            'name': 'OLK'
        },
        "0x2791Bca1f2de4661ED88A30C99A7a9449Aa84174": {
            'precision': 2,
            'decimals': 6,
            'label': <span>USDC<img src={usdc} width={25} height={25} /></span>,
            'explorer': 'https://polygonscan.com/',
            'name': 'USDC'
        },
        "0xD33572f6DD1bb0D99C8397c8efE640Cf973EaF3B": {
            'precision': 2,
            'decimals': 6,
            'label': <span>USDC TestNet<img src={usdc} width={25} height={25} /></span>,
            'explorer': 'https://polygonscan.com/',
            'name': 'USDC TestNet'
        },
        
    }

    constructor(props) {
        super(props);

        dataService.getData().subscribe(message => {
            this.state.web3 = message.value.web3;
            this.setState({ web3: message.value.web3, currentAccount: message.value.currentAccount });
        });

        let bet_amount = 0;

        props.play.play_amounts.forEach(element => {

            bet_amount = BigNumber(element).plus(BigNumber(bet_amount));
            
        });

        bet_amount = bet_amount.dividedBy(BigNumber(10).exponentiatedBy(BigNumber(Play.token_info[props.play.token]['decimals'])));

        bet_amount = bet_amount.toFixed(Play.token_info[props.play.token]['precision']);

        this.state = {
            web3: props.web3,
            currentAccount: props.currentAccount,
            play: props.play,
            bet_amount: bet_amount,
            label: Play.token_info[props.play.token]['label'],
            block_time: props.play.block_time
        };
        

    }

    componentDidMount() {


        let this_obj = this;

        setInterval(() => {
            this_obj.setState({
                block_time: this_obj.props.play.block_time,
                play: this_obj.props.play
            });
        }, 5000);
     
  
    }

    render() {

        let played_at = 60;
        let status_css = 'list-group-item-danger';

        if(this.state.block_time != undefined){
            played_at = parseInt(Date.now()/1000) - this.state.play.block_time;
        }

        let minutes = parseInt(played_at/60);

        let time_name = minutes.toString() + " minutes";

        if(minutes > 60*24){
            time_name = parseInt(minutes/(60*24)).toString() + " days";
        }

        let earnings = 0;
        if(parseInt(this.state.play.earnings) >0){
            status_css = 'list-group-item-success';
        }

        let unclaimed_win = false;
        if(this.state.play.earnings == 0 && this.state.play.play_numbers.includes(this.state.play.result.toString())){
            unclaimed_win = true;
            status_css = 'list-group-item-success';
        }

        earnings = BigNumber(this.state.play.earnings);
        if(earnings.isGreaterThan(BigNumber(0))){

            earnings = earnings.dividedBy(BigNumber(10).exponentiatedBy(BigNumber(Play.token_info[this.state.play.token]['decimals'])));
            earnings = Number(earnings.toString()).toFixed(Play.token_info[this.state.play.token]['precision']);
        }
        earnings = earnings.toString();

        if(this.props.winList){

            return (
                <span className={`list-group-item list-group-item-action ${status_css}`} aria-current="true" >

                    <div class="d-flex w-100 justify-content-between">
                    <BetCell is_simple={true} number={parseInt(this.state.play.result)} updateBet={() => {}} chipBet={0} disabled={true} />
                    <small class="mb-1">earnings: {earnings} {this.state.label} </small>
                    <small>{time_name} ago</small>
                    </div>
                    
                    <div class="d-flex w-100 justify-content-between" >        
                    <small>Bet: {this.state.bet_amount} {this.state.label}</small>
                    <small>PlayID: {this.state.play.play_id}</small>
                    <small>Player: 
                        <a target="_blank" href={Play.token_info[this.state.play.token]['explorer']+"address/"+this.state.play.player} >
                            {
                                this.state.play.player.substring(0,5)+"..."+this.state.play.player.substring(this.state.play.player.length-5)
                            }
                            </a> 
                    </small>
                    </div>

                    
                        
                    
                </span>
            );

        }


        if(unclaimed_win && minutes < 14){
            return(
                <span className={`list-group-item list-group-item-action ${status_css}`} aria-current="true" >
                    <div class="d-flex w-100 justify-content-between">
                    <BetCell is_simple={true} number={this.state.play.result} updateBet={() => {}} chipBet={0} disabled={true} />
                    <small>PlayID: {this.state.play.play_id}</small>
                    <small>{time_name} ago</small>
                    </div>
                    
                    
                    <p class="mb-1">
                        This bet remains unclaimed. Claim bet earnings before get expired, 12 minutes after play start.
                    </p>
                    <button className='btn btn-success btn-sm blink' onClick={() => {this.props.onClaim(this.state.play.play_id)}} >Claim Profit</button>
                    
                </span>
            );
            
        }

        if(unclaimed_win && minutes > 12){
            return(
                <a href="#" className={`list-group-item list-group-item-action ${status_css}`} aria-current="true" >
                    <div class="d-flex w-100 justify-content-between">
                    <BetCell is_simple={true} number={this.state.play.result} updateBet={() => {}} chipBet={0} disabled={true} />
                    <small class="mb-1">bet lost: {this.state.bet_amount} {this.state.label}</small>
                    <small>{time_name} ago</small>
                    </div>
                    
                    <small class="mb-1">This winner bet expired unclaimed. Claim bet before 12 minutes to avoid this.</small>
                    <br/>
                    <small>PlayID: {this.state.play.play_id}</small>
                </a>
            );
            
        }

        if(this.state.play.result_status == "pending"){

            return(

                <a href="#" class="list-group-item list-group-item-action list-group-item-warning" aria-current="true" >
                    <div class="d-flex w-100 justify-content-between">
                    <h5 class="mb-1">Bet Amount {this.state.bet_amount} {this.state.label}</h5>
                    
                    </div>
                    <p class="mb-1">Bet waiting 3 block confirms for results.</p>
                    <div class="spinner-grow spinner-grow-sm" role="status">
                      <span class="sr-only">Loading...</span>
                    </div>
                    <small>PlayID: {this.state.play.play_id}</small>
                </a>
                

            );

        }

        if(this.state.block_time == undefined){
            return(

                <span class="list-group-item list-group-item-action" aria-current="true" >
                    <div class="d-flex w-100 justify-content-between">
                    <h5 class="mb-1">Bet Amount {this.state.bet_amount} {this.state.label}</h5>
                    
                    </div>
                    <div class="spinner-grow spinner-grow-sm" role="status">
                      <span class="sr-only">Loading...</span>
                    </div>
                    <small>PlayID: {this.state.play.play_id}</small>
                </span>
                
            );
        }

        return (
            <span className={`list-group-item list-group-item-action ${status_css}`} aria-current="true" >
                <div class="d-flex w-100 justify-content-between">
                <BetCell is_simple={true} number={this.state.play.result} updateBet={() => {}} chipBet={0} disabled={true} />

                {earnings > 0 ?
                    (
                        <small class="mb-1">earnings: {earnings} {this.state.label}</small>

                    ):(
                        <small class="mb-1">bet lost: {this.state.bet_amount} {this.state.label}</small>
                    )

                }

                <small>{time_name} ago</small>
                </div>

                <div class="d-flex w-100 justify-content-between" > 

                {earnings > 0 &&
                    <small>Bet: {this.state.bet_amount} {this.state.label}</small>
                }
                <small>PlayID: {this.state.play.play_id}</small>
                </div>
                
            </span>
        );

    }

}


export default withRouter(Play);
